@import "minxin-colors";

// 
$default-color: $palette-grey-300;
$default-color-dark:  $palette-grey-400;
$default-color-light: $palette-grey-200;

// 
$primary-color: "245, 60, 50" !default;
$primary-color-dark: "230, 0, 18" !default;
$primary-color-light: "230, 0, 18" !default;

// 
$font-family-primary: "Open Sans", "Helvetica Neue", Arial, "Hiragino Sans GB", "Microsoft YaHei", sans-serif !default;
// 
$font-size-base: 14px !default;
// 
$text-color-base: $color-dark-contrast !default;  

// buttonselect
$border-radius: 3px !default;
// 
$border-color: "165, 173, 186" !default;
// hoverselectdropdowntabledatepickertreemenucalendar
$item-hover-bg-color-base: "235, 236, 240" !default;
// selectedselectmenu
$item-selected-bg-color-base: "247,247,247" !default;

// Button :
// 
$secondary-color: $palette-grey-300 !default;
$secondary-color-dark: $palette-grey-400 !default;
$secondary-color-light: $palette-grey-400 !default;
// 
$button-secondary-text-color: $color-light-contrast !default;

// Table 
// 
$table-header-background-color: "247,247,247";
// 
$table-header-text-color: "102, 102, 102";
// 
$table-border-color-base: "233,233,233";

// 
$color-primary: $palette-blue-600 !default;
$color-primary-dark: $palette-blue-800 !default;
$color-primary-light: $palette-blue-400 !default;

// 
$color-accent: $palette-green-600 !default;
$color-accent-dark: $palette-green-800 !default;
$color-accent-light: $palette-green-400 !default;


